          <!--秒数通过 过滤器处理成看得懂的样式-->
          <span style="user-select: none;">{{
              this.musicDuration | timeFormat
            }}</span>

          <!--音乐播放进度条-->
          <el-slider v-model="musicDuration" :max="totalDuration" @change="musicDurationChange"
