                {{ scope.row.al.name ? scope.row.al.name : "未知专辑" }}</p>
              <p v-else>{{ scope.row.al.name ? scope.row.al.name : "未知专辑" }}</p>
            </template>
          </el-table-column>

          <el-table-column label="时长" prop="dt" width="80px" align="center">
            <template scope="scope">
